body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
    background-color: #f1dbb6;
    flex-direction: row;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    opacity: .95;
}

li {
  list-style: none;
}

.navbar-item {
  padding-left: 50px;
}

.navbar-item:hover{
  opacity: .75;
}

.navbar-container {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: row;
  padding-right: 50px;
}

img {
  margin-left: 0.5vw;
}

h3 {
    margin: 0px 0.5vw;
}

a {
  text-decoration: none;
  color:  #883a3a;
  font-weight: bold;
}
.faq-container {
    display: flex;
    flex-direction: row;
    padding: 0px;
    width: 100vw;
}

.vertical-column {
    width: 10vw;
    background-color: #883a3a;
    height: inherit;
}

.faq-info-container {
    width: 80vw;
}

.faq-header {
    margin-top: 2.2vh;
}

.faq-btn {
    text-align: left;
    text-decoration: none;
    color: black;
    font-size: 1.1em;
    font-weight: bold;
}

.faq-btn:hover {
    text-decoration: none;
    color: rgb(107, 107, 107);
}

.btn-check:focus + .btn,
.btn:focus {
    outline: 0;
    box-shadow: none;
}

.card-header {
    text-align: left;
}

.card-body {
    padding: 1rem 2rem;
    font-size: 1.1em;
}
.brotherhood{
    
}

